import Weapon from './Weapon';
import Shield from './Shield';

/* Fighter class definition */
const MAX_LIFE = 100;
let ids = Math.ceil(Math.random()*1000000);
const weapons = [
    () => new Weapon("Super pe brillante",19),
    () => new Weapon("pe de bonne facture", 13),
    () => new Weapon("pe pointue un peu mousse", 10)
];

const shields = [
    () => new Shield("Bouclier en vibranioum", 17),
    () => new Shield("Grand bouclier en fer forg", 13),
    () => new Shield("Vieux bouclier pourri", 10)
];

class Fighter {
    constructor (id, emoji, name, strength, dexterity, canHaveWeapon = false, needWeapon = false, life = MAX_LIFE) {
        this.emoji = emoji;
        this.name = name;
        this.strength = parseInt(strength);
        this.dexterity = parseInt(dexterity);
        this.life = parseInt(life);
        this.defaultLife = parseInt(life);
        this.badChance = 0;
        this.winCount = 0;
        this.deathCount = 0;
        this.intelligence = Math.random()*2;
        this.weapon = null;
        this.shield = null;
        this.id = parseInt(id) || ids;
        this.attacks = 0;
        this.attacksSum = 0;
        this.attacksLastFight = 0;
        this.attacksSumLastFight = 0;
        this.canHaveWeapon = canHaveWeapon;
        this.needWeapon = needWeapon;
        ids = id > ids ? id + 1 : ids + 1;
    }

    setLooseWeaponOrShield(f) {
        this.addToPage = f;
    }

    looseWeapon(fighter) {
        let msg;
        switch(Math.ceil(Math.random() * 10)) {
            case 1:
                msg = <div><b className="loosing">{this.emoji + " " + this.name} a perdu son </b><b className="weapon">{this.weapon.name}</b><b className="loosing"> !</b></div>;
                break;
            case 2:
                msg =  <div><b className="loosing">{this.emoji + " " + this.name} a perdu son </b><b className="weapon">{this.weapon.name}</b><b className="loosing">  cause de {fighter.emoji + " " + fighter.name} !</b></div>;
                break;
            case 3:
                msg =  <div><b className="loosing">{fighter.emoji + " " + fighter.name} a cass </b><b className="weapon">l'{this.weapon.name}</b><b className="loosing"> de {this.emoji + " " + this.name}</b></div>;
                break;
            case 4:
                msg =  <div><b className="loosing">{fighter.emoji + " " + fighter.name} a aval </b><b className="weapon">l'{this.weapon.name}</b><b className="loosing"> de {this.emoji + " " + this.name}</b></div>;
                break;
            case 5:
                msg =  <div><b className="loosing">{fighter.emoji + " " + fighter.name} a gard </b><b className="weapon">l'{this.weapon.name}</b><b className="loosing"> de {this.emoji + " " + this.name} enfonce dans son flanc !</b></div>;
                break;
            case 6:
                msg =  <div><b className="loosing">{this.emoji + " " + this.name} a laiss tomber son </b><b className="weapon">{this.weapon.name}</b><b className="loosing"> !</b></div>;
                break;
            default:
                msg =  <div><b className="loosing">{this.emoji + " " + this.name} a laiss chaper son </b><b className="weapon">{this.weapon.name}</b><b className="loosing"> !</b></div>;
        }
        this.weapon = null;
        return msg;
    }

    looseShield() {
        const msg = <div><b className="loosing">{this.emoji + " " + this.name} a perdu son </b><b className="shield">{this.shield.name}</b><b className="loosing"> !</b></div>;
        this.shield = null;
        return msg;
    }

    win() {
        this.winCount += 1;
        if (this.weapon && this.weapon.durability) this.weapon.durability += Math.floor(Math.random() * 10);
        if (this.shield && this.shield.durability) this.shield.durability += Math.floor(Math.random() * 15);
    }

    death() {
        this.deathCount += 1;
    }

    getLife() {
        return this.name + " : " + Math.ceil(this.life);
    }

    getName() {
        return (this.shield?"":"") + (this.weapon? "" : "") + " " + this.emoji + " " + this.name;
    }

    getReverseName() {
        return this.emoji + " " + this.name + " " + (this.weapon? "" : "") + (this.shield?"":"");
    }

    giveWeapon() {
        this.weapon = weapons[Math.ceil(Math.random() * weapons.length)-1]();
    }

    giveShield() {
        this.shield = shields[Math.ceil(Math.random() * shields.length)-1]();
    }

    getDamage(fighter) {
        const weaponDamage = this.weapon && this.weapon.durability ? this.weapon.damage : 0;
        let attack = Math.random() * (this.strength + weaponDamage) + this.intelligence;
        let msg, looseShield, looseWeapon;
        const defAttack = attack;
        attack = (attack - fighter.getDefense())>0 ? attack-fighter.getDefense(): Math.random()>0.5? 1 : 0;
        if (attack <= 1)
            if ((Math.random() + Math.random() + Math.random() +Math.random() + this.dexterity/25 + this.badChance) > 3.2 || (this.canHaveWeapon && Math.random()>0.9) || (this.needWeapon && Math.random()>0.7 && Math.random()>0.7 && Math.random()>0.7)) {
             attack = ((Math.random()+this.badChance) * this.strength)-fighter.getDefense()/4;
             attack = attack<10 && this.canHaveWeapon? attack+10 : attack>17 && this.needWeapon? attack/2 : attack;
             msg = <div><span className="chance">{this.emoji + " " + this.name} russi  attaquer par chance ! (<b> {attack.toFixed(2)}pts de dgats</b> dont <b>{this.badChance.toFixed(2)}pts de dgats de chance</b> !)</span></div>;
             this.badChance = 0;
            } else this.badChance += 0.025;
        
        if (attack && this.weapon && !this.weapon.use()) looseWeapon = this.looseWeapon(fighter);

        if (defAttack && fighter.shield && !fighter.shield.use()) looseShield = fighter.looseShield();
        
        return [attack, msg, looseWeapon, looseShield];
    }

    getDefense() {
        return this.dexterity + (this.shield? this.shield.protection : 0) + this.intelligence*0.75;
    }

    fight(fighter) {
        if (this.life === 0) return null;
        let [attack, chance, looseWeapon, looseShield] = this.getDamage(fighter);
        this.attacksLastFight++;
        this.attacksSumLastFight += attack;
        fighter.life -= (fighter.life-attack >=0)? Math.abs(attack) : fighter.life;
        if (fighter.life === 0) {
            this.win();
            fighter.death();
        } else if (this.life === 0) {
            fighter.win();
            this.death();
        }
        
        return <>{chance}{looseWeapon?looseWeapon:<></>}{looseShield?looseShield:<></>}{fighter.life > 0 ? 
            <div>{this.getName()}  {fighter.emoji + " " + fighter.name}: {fighter.getLife()}</div> : 
            <div><br /><br /> {this.emoji + " " + this.name} a gagn ! ({this.getLife()})<br /> {fighter.emoji + " " + fighter.name} est mort</div>}
        </>;
    }

    getScore() {
        return <><div>{this.emoji + " " + this.name}</div><div><span id={"win" + this.id}>&nbsp;{this.winCount}</span></div><div><span id={"death" + this.id}>&nbsp;{this.deathCount}</span></div></>;
    }

    isAliveFct(f) {
        if (this.life) return f();
    }

    isAlive() {
        return (this.life);
    }
    heal(healthpoints) {
        this.life += healthpoints;
        return <div>{this.emoji + " " + this.name} a t soign de {Math.floor(healthpoints)} </div>;
    }
}

export default Fighter;